//==========================================================
//основные стили модального окна
.modal.open .modal__overlay,
.modal.open .modal__window {
    opacity: 1;
    z-index: 1000;
}
//==========================================================
.modal.hide .modal__overlay,
.modal.hide .modal__window {
    opacity: 1;
    z-index: 1000;
}
//!анимируем модальное окно при появлении
.modal.open .modal__window {
    transform: translateY(25px);
}
//!анимируем фон модального окна при появлении
.modal.open .modal__overlay {
    background-color: rgba(0, 0, 0, 0.849);
    // overflow: hidden;
    // overflow-y: hidden; /*для вертикального*/
    // overflow-x: hidden; /*для горизонтального*/
    // overflow: auto; // задал скрол для модального окна
    // overflow-x: auto;
    overflow-x: hidden; // задал скрол для модального окна
}
//!анимируем фон модального окна при исчизновении
.modal.hide .modal__window {
    transform: translateY(-200px);
}
//!анимируем фон модального окна при изчизновении
.modal.hide .modal__overlay {
    background-color: rgba(0, 0, 0, 0);
}
//==========================================================
.modal__window,
.modal__overlay {
    opacity: 0;
    z-index: -1;
}
.modal {
    &__overlay {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba($color: $color-black, $alpha: 0); //!анимируем фон модального окна...
        transition: background-color 0.4s ease-in 0s;
    }
    &__window {
        margin: 0 auto;
        border-radius: 5px;
        background: $color-bgModal;
        color: $color-white;
        transform: translateY(-200px); //!анимируем модальное окно...
        transition: transform 0.4s ease-in 0s;
    }
    &__header {
        padding: 10px 15px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #91919159;
    }
    &__title {
        font-size: 1.5rem;
    }
    &__close {
        cursor: pointer;
        font-size: 20px;
        &:hover {
            color: $color-LuminescentRed;
        }
    }
    &__body {
        padding: 10px;
    }
    &__footer {
        text-align: right;
        padding: 10px 20px;
        .btn-danger,
        .btn-primary {
            margin-left: 20px;
            padding: 0 10px;
            min-width: 100px;
            color: $color-black;
        }
    }
}
