.info {
    background: url("./../../img/bg.jpg") center/cover no-repeat;
    min-height: 100vh;
    position: relative;
    z-index: -2;
    padding: 16% 0;
    &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.5);
        z-index: -1;
    }
    &__body {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .info__content {
        max-width: 100%;
        position: absolute;
        top: 30%;
        left: 40%;
        transform: translate(-35%, -35%);
        color: $color-white;
        text-align: center;
        &-title {
            font-size: 2em;
            text-transform: uppercase;
        }
        &-text {
            font-size: 1.3em;
            p {
                padding: 6% 3%;
            }
        }
    }
}
//==========================================================================
//ADAPTIVE адаптив информационного блока
@media (max-width: 767px) {
    .info {
        .info__content {
            max-width: 100%;
            position: relative;
            top: 0%;
            left: 0%;
            transform: translate(0%, 0%);
            padding-top: 6%;
            &-title {
                font-size: 2.5em;
            }
            &-text {
                max-width: 700px;
                font-size: 1.6em;
                p {
                    padding: 4% 3%;
                }
            }
        }
    }
}
@media (max-width: 576px) {
    .info {
        .info__content {
            &-title {
                margin: 0 auto;
                font-size: 1.5em;
            }
            &-text {
                max-width: 700px;
                font-size: 1.2em;
                p {
                    padding: 4% 3%;
                }
            }
        }
    }
}
@media (max-width: 320px) {
    .info {
        .info__content {
            &-title {
                width: 266px; //236
            }
        }
    }
}
