.footer {
    background-color: $color-footer;
    padding: 8% 2% 2% 2%;
    // min-height: 100vh; //!для фуллскрина
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;
    &__title {
        display: flex;
        justify-content: space-around;
        align-items: center;
        &-left {
            word-spacing: 0.2em;
            font-size: 1.2em;
            letter-spacing: 0.1em;
            text-transform: uppercase;
            padding-right: 30%;
        }
        &-right {
            padding-right: 10%;
        }
    }
    .footer__content-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        img.maps {
            display: block;
            width: 600px;
        }
        a {
            color: $color-black;
            font-weight: 600;
        }
        .footer__contact {
            &-items {
                padding: 0 0 2em 0;
                li {
                    padding: 2% 0 2% 0;
                    a {
                        span {
                            display: block;
                        }
                    }
                }
            }
            &-social-items {
                display: flex;
                align-items: center;
                li {
                    padding-right: 6%;
                    a {
                        font-size: 2vw;
                    }
                }
            }
            &-maneger {
                display: flex;
                align-items: center;
                &-avatar {
                    flex: 0 0 60px;
                    border-radius: 50%;
                    overflow: hidden;
                    max-width: 3.8em;
                    max-width: 60px;
                    max-height: 60px;
                }

                &-name-consultant {
                    line-height: 1.5em;
                    font-weight: 700;
                    padding-left: 1.5em;
                    strong {
                        display: block;
                        font-weight: 300;
                    }
                }
            }
        }
    }
    &__copyright {
        padding: 0% 0% 0% 0%;
        text-align: center;
    }
}
//==========================================================================
//ADAPTIVE адаптив подвал сайта
@media (max-width: 992px) {
    .footer {
        .footer__content-info {
            img.maps {
                display: block;
                width: 550px;
            }
        }
    }
}
//==========================================================================
@media (max-width: 767px) {
    .footer {
        &__title {
            &-left {
                font-size: 1em;
                word-spacing: 0em;
                letter-spacing: 0em;
            }
            &-right {
                padding-right: 8%;
            }
        }
        .footer__content-info {
            img.maps {
                width: 370px;
            }
            .footer__contact {
                &-social-items {
                    li {
                        a {
                            font-size: 3vw;
                        }
                    }
                }
                &-maneger {
                    &-name-consultant {
                        line-height: 1em;
                        padding-left: 0.5em;
                    }
                }
            }
        }
    }
}
//==========================================================================
@media (max-width: 576px) {
    .footer {
        &__title {
            display: block;
            text-align: center;
            &-left {
                padding-right: 0%;
            }
            &-right {
                padding-right: 0%;
            }
        }
        .footer__content-info {
            display: block;
            position: relative;
            .footer__contact {
                &-items {
                    padding: 1.5% 0 1.5% 0;
                    li {
                        padding: 1%;
                    }
                }
                &-maneger {
                    position: absolute;
                    top: 62%;
                    left: 52%;
                }
                &-social-items {
                    padding-bottom: 1.5%;
                    li {
                        padding-right: 2.5%;
                        a {
                            font-size: 1.5em; // 4.5 vw
                        }
                    }
                }
            }
        }
    }
}
//==========================================================================
@media (max-width: 320px) {
    .footer {
        .footer__content-info {
            .footer__contact {
                &-maneger {
                    position: static;
                }
                &-social-items {
                    li {
                        a {
                            font-size: 1.2em;
                        }
                    }
                }
            }
        }
    }
}
