.reviews {
    min-height: 100vh; //!для фуллскрина
    display: flex;
    justify-content: center;
    align-items: center;
    &__body {
        display: flex;
        padding-left: 4em;
        justify-content: center;
        align-items: center;
        position: relative;
        //======================================
        // надпись в  футере Profi ZNO
        .method__content-bg-text {
            position: absolute;
            top: 85%;
            right: 10%;
            font-size: 16em; //12.9
            font-weight: 700;
            color: #a7a6a648;
        }
        //======================================
    }
    &__content {
        margin-left: 8em;
        &-text {
            max-width: 70%;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 2em;
        }
        span.icon-quotes-right {
            font-size: 2.5em;
        }
        // &-icons {
        //     span.icon-heart {
        //         font-size: 1.3em;
        //         padding-right: 0.5em;
        //         cursor: pointer;
        //         &:focus {
        //             color: $color-lghtgre;
        //         }
        //     }
        // }
    }
}
//====================================================================
//ADAPTIVE текста  над слайдером
@media (max-width: 992px) {
    .reviews__body {
        display: block;
    }
    .reviews__content {
        margin-left: 1em;
    }
}
@media (max-width: 767px) {
    .reviews {
        &__body {
            .method__content-bg-text {
                top: 102%;
                left: 12%;
                font-size: 5em;
            }
        }
    }
    .reviews__body {
        display: block;
    }
    .reviews__content {
        margin-left: 4em;
    }
}
//====================================================================
@media (max-width: 576px) {
    .reviews {
        &__body {
            .method__content-bg-text {
                top: 102%;
                left: 24%;
                font-size: 3.4em;
            }
        }
    }
    .reviews__content {
        margin-left: 5%;
        &-text {
            max-width: 100%;
        }
    }
}
//====================================================================
@media (max-width: 320px) {
    .reviews {
        &__body {
            .method__content-bg-text {
                display: none;
            }
        }
    }
    .reviews__content {
        margin-left: 0%;
        &-text {
            max-width: 100%;
        }
    }
}
