.courses {
    background-color: $color-footer;
    min-height: 100vh;
    padding-top: 8%;
    padding-bottom: 8%;
    position: relative;
    &__body {
        flex: 1 1 auto;
        max-width: 100%;
        margin: 0 auto;
    }
    //=========================================
    // фоновый желтый квадрат за карточками позиционирован к  body
    .yellow-square {
        position: absolute;
        top: -16%;
        left: 0;
        width: 600px;
        height: 400px;
        background-color: rgb(226, 180, 28);
        h2.corses__title {
            color: $color-white;
            text-align: center;
            font-size: 6vh;
            padding-top: 12%;
        }
    }
    &__contents {
        display: grid;
        grid-template: repeat(auto-fit, 390px) / repeat(auto-fit, 300px);
        gap: 2.5vh;
        justify-items: center;
        justify-content: space-evenly;
    }
    //=========================================
    // стилизация карточки
    .card {
        position: relative;
        width: 300px;
        height: 390px;
        background-color: $color-white;
        transform-style: preserve-3d;
        perspective: 38em;
        transition: 0.5s;
        box-shadow: 0 20px 20px -20px #333;
        overflow: hidden;
        cursor: pointer;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &__front {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: $color-white;
            backface-visibility: hidden;
            transform: rotateY(0deg);
            transition: 0.5s;
            //==========================================
            // затемнил карточки
            &:after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: rgba(0, 0, 0, 0.5);
                z-index: 1;
            }
            //==========================================
            // стилизация кнопки и заголовка
            h2,
            p {
                text-align: center;
                color: $color-white;
                position: absolute;
                z-index: 2;
            }
            h2 {
                top: 30%;
                left: 50%;
                transform: translate(-50%, -30%);
                font-size: 2em;
                line-height: 1em;
            }
            p {
                top: 80%;
                left: 20%;
                transform: translate(-20%, -80%);
                border: 2px solid $color-white;
                padding: 10px 20px;
                span.icon-arrow-right2 {
                    position: absolute;
                    top: -4px;
                    right: -60px;
                    font-size: 2em;
                }
            }
            //==========================================
            // стилизация границ кнопок
            .border__green {
                border: 2px solid green;
            }
            .border__yellow {
                border: 2px solid yellow;
            }
            .border__blue {
                border: 2px solid $color-twitter;
            }
            .border__red {
                border: 2px solid $color-LuminescentRed;
            }

            //==========================================
            // линия между заголовком и кнопкой
            &-line {
                position: absolute;
                top: 60%;
                left: 50%;
                width: 200px;
                transform: translate(-50%, -60%);
                z-index: 100;
                border: 1px solid $color-white;
            }
        }
        &.is-flipped .card__front {
            transform: rotateY(-180deg);
        }
        // &:hover .card__front {
        //     transform: rotateY(-180deg);
        // }
        //=========================================
        // задняя часть карточки
        &__back {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgb(216, 216, 216);
            backface-visibility: hidden;
            transform: rotateY(180deg);
            transition: 0.5s;
            .card__detals {
                position: absolute;
                top: 5%;
                width: 95%;
                padding-left: 1em;
                font-weight: 300;
                h2 {
                    color: #0099ff;
                    font-size: 1.4em;
                    text-align: center;
                }
                ul {
                    padding: 0;
                    li {
                        position: relative;
                        letter-spacing: 1px;
                        padding-left: 10px;
                        list-style-type: none;
                        &:before {
                            content: "-";
                            padding-right: 4%;
                            position: absolute;
                            left: 0;
                            top: 0;
                        }
                    }
                }
            }
        }
        // &:hover .card__back {
        //     transform: rotateY(0deg);
        // }
        &.is-flipped .card__back {
            transform: rotateY(0deg);
        }
    }
}
//==========================================================================
//ADAPTIVE адаптив блока курсов
@media (max-width: 767px) {
    .courses {
        .yellow-square {
            top: -20.5%;
            left: 50%;
            transform: translate(-50%, 20%);
            max-width: 100%;
            h2.corses__title {
                color: $color-white;
                text-align: center;
                font-size: 3em;
                padding-top: 8%;
            }
        }
        .card {
            display: flex;
            justify-content: center;
        }
    }
}
@media (max-width: 576px) {
    .courses {
        .yellow-square {
            top: -11.5%;
            max-width: 80%;
            height: 300px;
            left: 25%;
            h2.corses__title {
                padding-top: 10%;
                font-size: 2.5em;
            }
        }
    }
}
@media (max-width: 320px) {
    .courses {
        .yellow-square {
            margin-top: 50px;
            top: -12.5%;
            max-width: 60%;
            height: 300px;
            left: 25%;
            h2.corses__title {
                padding-top: 8%;
                font-size: 2.2em;
            }
        }
        .card {
            margin-top: 8%;
        }
    }
}
