.menu {
    /* ======================================================= */
    position: sticky;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 50;
    /* ======================================================= */
    /* шапка плашки  */
    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $color-footer;
        z-index: 2;
    }
    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 80px;
    }
    //==================================
    //LOGO стилизация  лого
    &__logo {
        color: $color-black;
        font-size: 3vw;
        line-height: 1;
        text-transform: uppercase;
        font-weight: 700;
        position: relative;
        z-index: 3;
    }
    //==================================
    //MENU стилизация  меню
    &__nav {
        &-list {
            display: flex;
            position: relative;
            z-index: 3;
            li {
                margin: 0 0 0 14vh;
            }
        }
        &-link {
            color: $color-black;
            font-size: 1.3vw;
            font-weight: 500;
        }
    }
    /* ======================================================= */
    /* BURGER */
    &__burger {
        display: none;
    }
}
/* ======================================================= */
//ADAPTIVE  бургер / меню
@media (min-width: 1200px) {
    .menu {
        .menu__nav-list {
            li {
                margin: 0 0 0 10vh;
                &:nth-child(1) {
                    margin: 0 0 0 0;
                }
            }
        }
    }
}
@media (max-width: 767px) {
    /* ======================================================= */
    /* замок от скрола при активном меню бургера */
    body.lock {
        overflow: hidden;
    }

    .menu {
        position: fixed;
        &__content {
            height: 60px;
        }
        &:before {
            content: "";
            background-color: transparent;
        }
        &__logo {
            display: none;
        }
        /* ======================================================= */
        /* отрисовка бургера */
        &__burger {
            display: block;
            position: relative;
            top: 56%;
            right: -90%;
            width: 30px;
            height: 20px;
            z-index: 3;
            span {
                position: absolute;
                background-color: $color-white;
                width: 100%;
                height: 2px;
                left: 0;
                top: 9px;
                box-shadow: 2px 2px 4px $color-black;
            }
            &:before,
            &:after {
                content: "";
                background-color: $color-white;
                position: absolute;
                width: 100%;
                height: 2px;
                left: 0;
                transition: all 0.4s ease 0s;
                box-shadow: 2px 2px 4px $color-black;
            }
            &:before {
                top: 0;
            }
            &:after {
                bottom: 0;
            }
            /* ======================================================= */
            /* отрисовка при классе актив изменение на крестик */
            &.active span {
                transform: scale(0);
                transition: all 0.4s ease 0s;
            }
            &.active:before {
                transform: rotate(45deg);
                top: 9px;
                background-color: $color-black;
            }
            &.active:after {
                transform: rotate(-45deg);
                bottom: 9px;
                background-color: $color-black;
            }
        }
        /* ======================================================= */
        /* стилизация меню */
        .menu__nav {
            position: fixed;
            /* убрал фон меню на минус 100% */
            top: -100%;
            left: 0;
            width: 100%;
            height: 100%;
            overflow: auto;
            background: rgba(255, 255, 255, 1);
            background: linear-gradient(
                to bottom,
                rgba(255, 255, 255, 1) 0%,
                rgba(246, 246, 246, 1) 47%,
                rgba(237, 237, 237, 1) 100%
            );
            transition: all 0.5s ease 0s;
            /* ======================================================= */
            /* стилизация меню  при классе актив*/
            &.active {
                top: 0;
            }
            &-list {
                display: block;
                padding-top: 16%;
                li {
                    margin: 0px 0px 20px 0px;
                    padding: 20px 0px 0px 0px;
                    display: flex;
                    justify-content: center;
                    padding: 3% 0;
                }
            }
            &-link {
                font-size: 1.5em;
                font-weight: 700;
            }
        }
    }
}
@media (max-width: 320px) {
    .menu {
        &__burger {
            top: 35%;
        }
    }
}
