.header {
    //================================================
    //общие настройки
    position: relative;
    color: $color-white;
    min-height: 100vh; //!для фуллскрина
    padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
    overflow: hidden;
    height: 0;
    z-index: 3;

    ul {
        display: flex;
    }
    a {
        color: $color-white;
    }
    //================================================
    //фон HEADER

    video {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        position: absolute;
        z-index: -2;
        object-fit: cover;
    }

    &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.6);
        z-index: -1;
    }
    //================================================
    //стилизация контента
    .wrapp {
        display: flex;
        justify-content: space-between;
        padding: 1% 2% 0% 2%;
    }
    //================================================
    //LOGO логотип сайта
    &__logo {
        text-shadow: 10px 3px 6px $color-black;
        &-link {
            color: $color-white;
            font-size: 2.5vw;
            line-height: 1;
            text-transform: uppercase;
            font-weight: 700;
            span.icon-graduation {
                font-size: 3vw;
            }
        }
    }
    //================================================
    //CONTACT контакты
    &__contact {
        font-size: 1.2vw;
        font-weight: 500;
        letter-spacing: normal;
        &-mail {
            padding-right: 2em;
        }
        span {
            font-size: 1.5vw;
            padding-right: 0.4em;
        }
    }
    //================================================
    //SOCIAL социальные иконки
    &__social {
        font-size: 1.5vw;
        &-list {
            padding-right: 0.5em;
        }
    }
    //================================================
    &__contents {
        text-align: center;
        //================================================
        //TITLE заголовок
        &-title {
            padding-top: 1em;
            margin: 0 auto;
            line-height: 1;
            font-size: 3vw;
            max-width: 90%;
        }
        //================================================
        //BUTTON  кнопка в хедер
        &-btn {
            padding: 4em;
            &-sms {
                border: 0.1vw solid $color-white;
                padding: 1.5% 2%;
                border-radius: 3vw;
                font-size: 1.6vw;
                font-weight: 700;
                text-shadow: 4px 6px 13px #2b2b318e;
                span {
                    color: $color-yellow;
                }
                &:hover {
                    background-color: #ffffff79;
                    transition: 1.5s;
                    color: $color-yellow;
                }
            }
        }
        //================================================
        //TEXT под кнопкой
        &-text {
            font-weight: 600;
            font-size: 1.6vw;
            padding-bottom: 3em;
        }
        //================================================
    }
    &__footer {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding-top: 40px;
        &-blocks1,
        &-blocks2,
        &-blocks3 {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 220px;
            height: 120px;
            border-left: 1px solid $color-white;
            border-right: 1px solid $color-white;
            border-bottom: 1px solid $color-white;
            text-align: center;
            font-weight: 500;
            font-size: 1.1em;
            margin: 1%;
            //================================
            // отрисовка верхней границы  бордера
            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 80px;
                height: 1px;
                background-color: #fff;
                display: inline-block;
            }
            &::after {
                content: "";
                position: absolute;
                top: 0;
                right: 0;
                width: 80px;
                height: 1px;
                background-color: #fff;
                display: inline-block;
            }
        }
        &-blocks1,
        &-blocks2,
        &-blocks3 {
            position: relative;
            span {
                position: absolute;
                top: -25%;
                text-align: center;
                font-size: 6vh;
            }
        }
    }
}
/* ======================================================= */
//ADAPTIVE  хедера
@media (max-width: 767px) {
    .header {
        //===================================================================
        //LOGO header
        &__logo {
            &-link {
                display: block;
                font-size: 2.5vw;
                span.icon-graduation {
                    font-size: 4vw;
                }
            }
        }
        //================================================
        //CONTACT контакты
        &__contact {
            padding: 1em 0 1em 0;
            font-size: 2vw;
            &-mail {
                padding-right: 1em;
            }
            span {
                font-size: 2.5vw;
                padding-right: 0.4em;
            }
        }
        //================================================
        //SOCIAL социальные иконки
        &__social {
            font-size: 2vw;
        }
        //================================================
        &__contents {
            //================================================
            //TITLE заголовок
            &-title {
                padding-top: 4%;
                font-size: 4vw;
                max-width: 100%;
            }
            //================================================
            //BUTTON  кнопка в хедер
            &-btn {
                padding: 2em;
                &-sms {
                    padding: 2% 2%;
                    font-size: 2.5vw;
                    &:hover {
                        color: $color-yellow;
                    }
                }
            }
            //================================================
            //TEXT под кнопкой
            &-text {
                font-size: 2.6vw;
                padding: 0 1em;
                max-width: 80%;
            }
        }
        //================================================
        &__footer {
            padding-top: 12%;
            &-blocks1,
            &-blocks2,
            &-blocks3 {
                max-width: 200px;
                max-height: 100px;
                font-size: 1em;
                span {
                    top: -35%;
                    font-size: 2em;
                }
                //================================
                // отрисовка верхней границы  бордера
                &::before {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 35%;
                    height: 1px;
                    background-color: #fff;
                    display: inline-block;
                }
                &::after {
                    content: "";
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 35%;
                    height: 1px;
                    background-color: #fff;
                    display: inline-block;
                }
            }
        }
        //================================================
    }
}
//==========================================================================
@media (max-width: 576px) {
    .header {
        .wrapp {
            display: block;
        }
        //===================================================================
        //LOGO header
        &__logo {
            display: inline-block;
            &-link {
                display: block;
                font-size: 2em;
                span.icon-graduation {
                    font-size: 1em;
                }
            }
        }
        //================================================
        //================================================
        //CONTACT контакты
        &__contact {
            padding-top: 6%;
            font-size: 1.1em;
            &-mail {
                padding-right: 1vh;
            }
            span {
                font-size: 0.8em;
            }
        }
        //================================================
        //SOCIAL социальные иконки
        &__social {
            position: absolute;
            top: 0;
            right: 0;
            font-size: 1.5em;
        }
        //================================================
        &__contents {
            //================================================
            //TITLE заголовок
            &-title {
                padding-top: 14%;
                font-size: 1.5em;
                max-width: 100%;
                letter-spacing: 1px;
                line-height: 28px;
            }
            //================================================
            //BUTTON  кнопка в хедер
            &-btn {
                padding: 16%;
                &-sms {
                    padding: 7% 6%;
                    font-size: 1.4em;
                    border-radius: 8vw;
                }
            }
            //================================================
            //TEXT под кнопкой
            &-text {
                font-size: 1.5em;
                font-weight: 300;
                max-width: 100%;
                margin: 0 auto;
                padding: 0;
                letter-spacing: 0px;
                line-height: 28px;
            }
            //================================================
        }
        //================================================
        &__footer {
            display: none;
        }
    }
    //================================================
}
//==========================================================================
@media (max-width: 320px) {
    .header {
        max-width: none;
        //LOGO header
        &__logo {
            &-link {
                font-size: 1.5em;
                span.icon-graduation {
                    font-size: 0.7em;
                }
            }
        }
        //================================================
        //CONTACT контакты
        &__contact {
            padding: 1em 0 1em 0;
            font-size: 0.9em;
            span {
                font-size: 1em;
                padding-right: 0.4em;
            }
        }
        //================================================
        //SOCIAL социальные иконки
        &__social {
            font-size: 1.2em;
        }
        //================================================
        &__contents {
            //================================================
            //TITLE заголовок
            &-title {
                padding-top: 10%;
                font-size: 1.6em;
            }
            //================================================
            //BUTTON  кнопка в хедер
            &-btn {
                padding: 16%;
                &-sms {
                    padding: 6% 6%;
                    font-size: 1.2em;
                }
            }
            //================================================
            //TEXT под кнопкой
            &-text {
                font-size: 1.1em;
                padding: 0 3%;
            }
            //================================================
        }
    }
    //================================================
}
