//===============================================================
//общие настройки блока
.method {
    min-height: 100vh;
    &__body {
        flex: 1 1 auto;
        max-width: 100%;
        margin: 0 auto;
        align-items: center;
    }
    &__content {
        display: flex;
        position: relative;
        justify-content: space-around;
        align-items: center;
        padding-top: 10%;
        &-title {
            padding-right: 60px;
            font-size: 2em;
            text-transform: uppercase;
        }
        &-bg-text,
        &-bg-text2 {
            position: absolute;
            right: 10%;
            font-size: 38vw;
            font-weight: 700;
            color: #a7a6a648;
        }
        &-bg-text2 {
            display: none;
        }
        &-text {
            max-width: 55%;
            padding-top: 6%;
            text-align: left;
            font-size: 1.1em;
        }
    }
    //===============================================================
    .timeline {
        display: flex;
        justify-content: space-between;
        position: relative;
        padding-top: 12%;
        padding-bottom: 8%;
        &__menu {
            width: 371px;
            &-contents {
                //=======================================================
                //стилизация текста по умолчанию с левой стороны
                text-align: right;
                max-height: 50px;
                overflow: hidden;
                h3 {
                    padding-top: 12px;
                    padding-bottom: 12px;
                    cursor: pointer;
                    span {
                        padding-right: 5px;
                        opacity: 0;
                    }
                }
                //=======================================================
                // стилизация чисел при адаптиве  576px
                .number {
                    display: none;
                    text-align: left;
                    font-size: 2.8em;
                    font-weight: 700;
                    padding-left: 10%;
                    color: rgb(148, 147, 147);
                    cursor: pointer;
                }
                // p {
                //     padding-bottom: 8px;
                // }
                // h4 {
                //     text-align: left;
                // }
                &.is-open {
                    max-height: 40%;
                    transition: 0.5s ease 0s;
                }
                //=======================================================
                //ACTIVE class
                //стилизация  линии при активном состоянии
                &.active:before {
                    content: "";
                    position: absolute;
                    left: 401px;
                    width: 2px;
                    height: 100%;
                    background: $color-LuminescentRed;
                }
                //стилизация текста контента при активном состоянии
                //с левой стороны
                &.active h3 {
                    font-weight: 700;
                    font-size: 1.5em;
                    color: $color-LuminescentRed;
                    transition: 0.3s ease 0s;
                }
                //==============================================
                //стили  кругов в активном состоянии
                &.active h3:after {
                    background: $color-UltramarineBlue;
                }
                &.active {
                    span {
                        opacity: 1;
                        transition: 1s ease 0s;
                    }
                }
                //==============================================
            }
            ul {
                list-style: none;
                padding: 0;
                li {
                    position: relative;
                    padding-bottom: 30px;
                    h3:after {
                        content: "";
                        position: absolute;
                        width: 14px;
                        height: 14px;
                        top: -1px;
                        right: -10.5%;
                        border-radius: 50%;
                        background: #ffff00;
                        box-shadow: 0 0 0 3px #42b3cf2a;
                    }
                }
                //=================================
                //стилизация  линии по умолчанию
                &:before {
                    content: "";
                    position: absolute;
                    left: 401px;
                    width: 2px;
                    height: 65%;
                    background: #c5c5c5;
                }
            }
        }
        &__sections-contents {
            max-width: 60%; //! временно
            h3 {
                padding-bottom: 8px;
                span.icon-pushpin {
                    padding-right: 5px; //!спаны повтор временно
                    color: $color-fsbook;
                }
            }
            p {
                padding-bottom: 8px;
            }
            //=======================================================
            //ACTIVE class
            //стилизация  линии при активном состоянии
            &.active {
                opacity: 1;
            }
        }
    }
}
//==========================================================================
//ADAPTIVE блока с таймлайн и блока с текстом
@media (max-width: 992px) {
    .method {
        .timeline {
            &__menu {
                width: 371px;
                &-contents {
                    h3 {
                        text-align: center;
                        padding: 8px 10% 0px 8px;
                    }
                    &.active:before {
                        left: 92.5%;
                    }
                }
                ul {
                    li {
                        h3:after {
                            top: 0;
                            left: 91%;
                        }
                    }
                    &:before {
                        left: 36%;
                        height: 60%;
                    }
                }
            }
        }
    }
}
//==========================================================================
@media (max-width: 767px) {
    .method {
        &__content {
            &-text {
                max-width: 66%;
            }
        }
        .timeline {
            &__menu {
                width: 371px;
                &-contents {
                    h3 {
                        text-align: left;
                        padding: 8px 0px 0px 8px;
                    }
                    &.active:before {
                        left: 0px;
                    }
                }
                ul {
                    li {
                        h3:after {
                            top: 0;
                            left: -6px;
                        }
                    }
                    &:before {
                        left: 0px;
                    }
                }
            }
        }
    }
    .contents-item-video {
        position: relative;
        padding-bottom: 56.25%; /* 16:9 */
        height: 0;
        iframe {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
}
//==========================================================================
@media (max-width: 576px) {
    .method {
        &__content {
            display: block;
            &-title {
                text-align: center;
                padding-right: 0px;
                font-size: 1.5em;
            }
            &-text {
                margin: 0 auto;
                text-align: center;
                max-width: 100%;
                font-size: 1.2em;
                letter-spacing: 0.1px;
            }
            &-bg-text {
                top: 30%;
                right: 30%;
                transform: translate(-30%, -30%);
            }
            &-bg-text2 {
                display: block;
                top: 64%;
                right: -10%;
                transform: translate(-18%, -16%);
            }
        }
        .timeline {
            &__menu {
                width: 371px;
                &-contents {
                    h3 {
                        display: none;
                        // visibility: hidden;
                    }
                    .number {
                        display: block;
                        &:after {
                            content: "";
                            position: absolute;
                            width: 14px;
                            height: 14px;
                            top: 0;
                            left: -6px;
                            border-radius: 50%;
                            background: #ffff00;
                            box-shadow: 0 0 0 3px #42b3cf2a;
                        }
                    }
                    &.active .number {
                        color: $color-UltramarineBlue;
                        font-size: 4.5em;
                    }
                    &.active:before {
                        left: 0px;
                    }
                    &.active .number:after {
                        background: $color-UltramarineBlue;
                    }
                }
                ul {
                    &:before {
                        left: 0px;
                        height: 80%;
                    }
                }
            }
        }
    }
}
//==========================================================================
@media (max-width: 320px) {
    .method {
        .timeline {
            &__menu {
                ul {
                    &:before {
                        height: 84%;
                    }
                }
            }
        }
    }
}
