.slider {
    position: relative;
    overflow: hidden;
    max-width: 59%;
    margin: 0 auto;
    padding: 30px 40px;
    &__wrapper {
        display: flex;
        transition: transform 0.6s ease;
    }
    &__item {
        flex: 0 0 100%;
        max-width: 100%;
        padding-left: 10px;
        padding-right: 10px;
        //======================================
        //! расстянул блоки на всю высоту
        display: flex;
        flex-direction: row;
        align-items: stretch;
        //======================================
    }
    &__content {
        box-shadow: 1px 1px 2px $color-black;
        padding: 30px 20px;
        border: none;
        border-bottom: 2px solid $color-fsbook;
        &-header {
            display: flex;
            align-items: center;
            padding: 20px;
        }
        &-avatar {
            flex: 0 0 50px;
            border-radius: 50%;
            overflow: hidden;
            max-width: 50px;
            max-height: 50px;
        }
        &-photo {
            display: block;
            max-width: 100%;
            max-height: 100%;
        }
        &-author {
            margin-left: 60px;
            line-height: 2.5em;
            font-weight: 600;
        }
        &-reviews {
            text-align: left;
            p {
                font-style: italic;
                &:last-child {
                    padding-top: 4%;
                    font-weight: 700;
                }
            }
        }
    }
    &__indicators {
        position: absolute;
        right: 0;
        top: -2%;
        left: 0;
        z-index: 15;
        display: flex;
        justify-content: center;
        padding-left: 0;
        margin: 0 15% 0 15%;
        list-style: none;
        li {
            box-sizing: content-box;
            flex: 0 1 auto;
            width: 30px;
            height: 4px;
            margin-right: 3px;
            margin-left: 3px;
            text-indent: -999px;
            cursor: pointer;
            background-color: #5e5e5e;
            background-clip: padding-box;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
            opacity: 0.5;
            transition: opacity 0.6s ease;
        }
        li.active {
            opacity: 1;
        }
    }
    &__control {
        position: absolute;
        top: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        color: $color-lghtgre;
        text-align: center;
        background: $color-white;
        z-index: 6;
        &-left {
            left: 0;
        }
        &-right {
            right: 0;
        }
        &::before {
            content: "";
            display: inline-block;
            width: 20px;
            height: 20px;
            background: transparent no-repeat center center;
            background-size: 100% 100%;
        }
        &-left::before {
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23424242' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
        }
        &-right::before {
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23424242' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
        }
    }
}

//====================================================================
//ADAPTIVE Slider
@media (min-width: 1200px) {
    .slider {
        max-width: 60%;
    }
    .slider__item {
        flex: 0 0 70%;
        max-width: 70%;
    }
    .slider__content-header {
        display: flex;
    }
}
@media (min-width: 992px) {
    .slider {
        max-width: 100%;
    }
    .slider__item {
        flex: 0 0 33.333333%;
        max-width: 33.333333%;
    }
}
@media (max-width: 767px) {
    .slider {
        max-width: 100%;
    }
}
@media (min-width: 576px) {
    .slider {
        padding: 20px 30px;
    }
    .slider__item {
        flex: 0 0 50%;
        max-width: 50%;
    }
}
@media (min-width: 320px) {
    .slider {
        max-width: 100%;
    }
    .slider__item {
        flex: 0 0 100%;
        max-width: 100%;
    }
    .slider__content-author {
        margin-left: 10px;
    }
    .slider__content {
        &-header {
            display: block;
        }
        &-reviews {
            text-align: center;
        }
    }
}

